import React from 'react';
import { setDropdown, updateUsers} from "../../../../redux/licenses/corporate/users/users.actions";
import {
    setAccessRightsForOneUserModal,
    setAuthorizationDataForOneUserModal,
    setLicenseDaysSettingsModal
} from "../../../../redux/app/app.actions";

import {connect} from "react-redux";
import { ClickAwayListener } from '@material-ui/core';

const TableActionDropdown = (props) => {

    const handleClickAway = () => {
        props.setDropdown(false, props.id);
    };

    const handleItem = (type) => {

        switch (type){
            case 'get data':
                props.setAuthorizationDataForOneUserModal({bool: true, data: props.id});

                break;
            case 'set rights':
                props.setAccessRightsForOneUserModal({bool: true, data: props.id});

                break;
            case 'delete user':
                let dataArray = [];

                props.usersTabData.map((i, key) => {
                    if(i.user.id !== props.id) {
                        dataArray.push(i);
                    }
                });

                props.updateUsers(dataArray);

                break;
        }

    }

    return (<ClickAwayListener onClickAway={handleClickAway}>
        <div className="table_action_dropdown">
            <ul>
                <li onClick={()=>{handleItem('get data')}}>Get authorization data</li>
                <li onClick={()=>{handleItem('set rights')}}>Set access rights</li>
                <li onClick={()=>{handleItem('delete user')}}>Delete user</li>
            </ul>
        </div>
    </ClickAwayListener>);
};


const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setDropdown: (bool, id) => dispatch(setDropdown(bool, id)),
        setAccessRightsForOneUserModal: (bool, id) => dispatch(setAccessRightsForOneUserModal(bool, id)),
        setAuthorizationDataForOneUserModal: (bool, id) => dispatch(setAuthorizationDataForOneUserModal(bool, id)),
        setLicenseDaysSettingsModal: (bool, id) => dispatch(setLicenseDaysSettingsModal(bool, id)),
        updateUsers: (data) => dispatch(updateUsers(data)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TableActionDropdown);