import React from 'react';
import cogIcon from './cog.svg';
import {setLicenseDaysSettingsModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";

const DaysLeft = (props) => {
    return (
        <div className="tableCell-daysLeft">
            <div className="value">
            {props.rowData.userOptions.daysLeft}
            </div>
            <div className="days">
                {props.rowData.userOptions.maxDaysLeft}
            </div>
            <div className="btn" onClick={()=>{
                props.setLicenseDaysSettingsModal({bool: true, data: props.rowData});
            }}>
                <img src={cogIcon} alt=""/>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (obj) => dispatch(setLicenseDaysSettingsModal(obj)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DaysLeft);