import React from 'react';
import Minus from "./minus.svg";
import Plus from "./plus.svg";
import { setConfigLimit} from "../../../redux/licenses/corporate/users/users.actions";
import {connect} from "react-redux";

const ConfigLimits = (props) => {

    return (
        <div className="tableCell-configLimits">
            <div className="value">
                {props.dataValue.userOptions.configsLimits.val}
            </div>
            <div className="days">
                {props.dataValue.userOptions.configsLimits.max}
            </div>
            <div className="btn--configLimits" onClick={()=>{
                 if(props.dataValue.userOptions.configsLimits.val > 0) {
                    props.setConfigLimit({id:props.dataValue.user.id , configLimit: props.dataValue.userOptions.configsLimits.val - 1})
                }
            }}>
                <img src={Minus} alt=""/>
            </div>
            <div className="btn--configLimits" onClick={()=>{
                    if(props.dataValue.userOptions.configsLimits.val < props.dataValue.userOptions.configsLimits.max) {
                        props.setConfigLimit({id: props.dataValue.user.id, configLimit: props.dataValue.userOptions.configsLimits.val + 1})
                    }
            }}>
                <img src={Plus} alt=""/>
            </div>
        </div>
    );
};


const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setConfigLimit: (obj) => dispatch(setConfigLimit(obj)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ConfigLimits);