import React from 'react';

import TableUserProfile from "../../../TableComponents/UserProfile/UserProfile";
import ConfigLimits from "../../../TableComponents/ConfigLimits/ConfigLimits";
import DaysLeft from "../../../TableComponents/DaysLeft/DaysLeft";
import {connect} from "react-redux";
import Switch from '@material-ui/core/Switch';
import {setCosts, setUsers, setActivity, sortTableByType, setSelect, setDropdown} from "../../../../redux/licenses/corporate/users/users.actions";
import {setCurrentLicense, setCurrentTab, setAdditionalScreenCorporate} from "../../../../redux/licenses/licenses.actions";
import {setLicenseDaysSettingsModal} from "../../../../redux/app/app.actions";

import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell/TableCell";
import TableBody from "@material-ui/core/TableBody";
import {IconPack} from "../../../../Images/IconPack";
import InputLabel from "@material-ui/core/InputLabel/InputLabel";
import TextField from "@material-ui/core/TextField/TextField";
import Select from "@material-ui/core/Select/Select";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Checkbox from "@material-ui/core/Checkbox/Checkbox";
import Button from "@material-ui/core/Button";
import { FormattedMessage } from 'react-intl';
import ClickAwayListener from '@material-ui/core/ClickAwayListener';
import TableActionDropdown from './tableActionDropdown';


import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails/AccordionDetails";
import Accordion from "@material-ui/core/Accordion/Accordion";


const CorporateUsers = (props) => {

    const [searchValue, setSearchValue] = React.useState('');
    const [orderValue, setOrderValue] = React.useState(false);
    const [orderStatus, setOrderStatus] = React.useState(false);
    const [wasChecked, setCheckedState] = React.useState(false);



    const handleChange = (rowData, type, event) => {

        switch (type) {
            case 'activity':
                props.setActivity(!rowData.userOptions.seesActivity, rowData.user.id);
                break;
            case 'users':
                props.setUsers(!rowData.userOptions.seesUsers, rowData.user.id);
                break;
            case 'costs':
                props.setCosts(!rowData.userOptions.seesCosts, rowData.user.id);
                break;
            default:
                break;

        }
    };

    let selectedDataClone = [];
     props.usersTabData.map((i)=> {
        if(i.userOptions.isSelected) {
            selectedDataClone.push(i);
        }
    });


    const [tableSort, setSort] = React.useState('Date of register');


    const handleSort = (event) => {
        setSort(event.target.value);

        if (event.target.value === 'Date of register') {
            props.sortTableByType('byRegistration');
        } else if (event.target.value === 'Date of last visit') {
            props.sortTableByType('byVisit');
        }
    };


    const tableCheckBox = (row) => {
        return (<Checkbox color="primary"  checked={row.userOptions.isSelected} onChange={()=> {
            props.setSelect(!row.userOptions.isSelected, row.user.id);
            setCheckedState(true);
        }}/>);
    }

    const handleClick = (rowData) => {
        props.setDropdown(!rowData.userOptions.isOpenedDropdown, rowData.user.id);
    }
    const [expanded, setExpanded] = React.useState('panel1');

    const handleAccordion = (panel) => (event, newExpanded) => {
        setExpanded(newExpanded ? panel : false);
    };

    const tableItem = (rowData, key) => {
        return (
            <Accordion square expanded={expanded === 'panel' + key} onChange={handleAccordion('panel' + key)}>
                <AccordionSummary aria-controls="panel1d-content" id="panel1d-header">
                    <h2>Collapsible Group Item {key}</h2>
                </AccordionSummary>
                <AccordionDetails>
                    <p  >
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse malesuada lacus ex,
                        sit amet blandit leo lobortis eget. Lorem ipsum dolor sit amet, consectetur adipiscing
                        elit. Suspendisse malesuada lacus ex, sit amet blandit leo lobortis eget.
                    </p>
                </AccordionDetails>
            </Accordion>


            //     <TableRow key={key} className={rowData.userOptions.isSelected ? 'active' : null}>
            //
            //     <TableCell className="padding-sides-5">{rowData.user.telegram}</TableCell>
            //     <TableCell className="padding-sides-20 white-space-normal">{rowData.user.comment}</TableCell>
            //
            //     <TableCell className="padding-sides-20">
            //         <Switch checked={rowData.userOptions.seesActivity} onChange={(e) => {
            //             handleChange(rowData, 'activity', e)
            //         }}/></TableCell>
            //
            //     <TableCell className="padding-sides-25"> <Switch checked={rowData.userOptions.seesUsers} onChange={(e) => {
            //         handleChange(rowData, 'users', e)
            //     }} /></TableCell>
            //
            //     <TableCell className="padding-sides-25"> <Switch checked={rowData.userOptions.seesCosts} onChange={(e) => {
            //         handleChange(rowData, 'costs', e)
            //     }} /></TableCell>
            //
            //     <TableCell className="padding-sides-25"><DaysLeft rowData={rowData}/></TableCell>
            //     <TableCell className="padding-sides-0 va-top relative o-visible"><ConfigLimits dataValue={rowData}/>
            //
            //         {rowData.userOptions.isOpenedDropdown ? <TableActionDropdown id={rowData.user.id}></TableActionDropdown> : null }
            //         <div className="btn--pins" onClick={() => {handleClick(rowData)}}>
            //               {IconPack.buttons.pins}
            //         </div>
            //     </TableCell>
            // </TableRow>
        )
    };

    let searchedData = [];

    if(orderValue){
        searchedData = props.usersTabData.filter(i => {

            if (i.user.name.toLowerCase().includes( searchValue.toLowerCase() ) ||
                i.user.id.toLowerCase().includes(searchValue.toLowerCase()) ||
                i.user.telegram.toLowerCase().includes(searchValue.toLowerCase()) ||
                i.user.comment.toLowerCase().includes(searchValue.toLowerCase())
            ) {
                return i;
            }
        }).map((i,key)=> {
            return tableItem(i, key)
        })
    }else {
        searchedData = props.usersTabData.filter(i => {
            if (i.user.name.toLowerCase().includes( searchValue.toLowerCase() ) ||
                i.user.id.toLowerCase().includes(searchValue.toLowerCase()) ||
                i.user.telegram.toLowerCase().includes(searchValue.toLowerCase()) ||
                i.user.comment.toLowerCase().includes(searchValue.toLowerCase())) {
                    return i;
            }
        }).map((i,key)=> {
            return tableItem(i, key)
        }).reverse();
    }




    let tbodyData = [];

    if(orderValue){
        tbodyData = props.usersTabData.map((rowData,key) => {
            return tableItem(rowData,key);
        })
    }else {
        tbodyData = props.usersTabData.map((rowData,key) => {
            return tableItem(rowData,key);
        }).reverse();
    }


    const orderHandler = () => {
        if(!orderStatus){
            setOrderStatus(true);
            setOrderValue(true);
        }else {
            if(orderValue === false) {
                setOrderStatus(false);
            }else {
                setOrderValue(false);
            }
        }

    };

    const getOrderClasses = () => {
        let classes;
        if(orderStatus) {
            if (!orderValue) {
                classes = 'filterPanel__select__order filterPanel__select__order--active';
            } else {
                classes = 'rotate-child-180  filterPanel__select__order filterPanel__select__order--active';
            }
        }else {
            classes = 'filterPanel__select__order';
        }

        return classes;
    }


    return (
        <div className="ÑustomStyle">
            <div className="filterPanel filterPanel--mobile filterPanel--users">

                <div className="position-relative w100percent mb-15">
                    <FormattedMessage  id='filterPanel.search'>
                        {(msg) => ( <TextField
                            variant="filled"
                            fullWidth
                            className="search_area"
                            placeholder={msg}
                            value={searchValue}
                            onChange={(e)=>{setSearchValue(e.target.value)}}
                        >
                        </TextField>)}
                    </FormattedMessage>

                </div>

                <FormControl variant="filled" className='filterPanel__select w160 mr10'>

                    <FormattedMessage  id='filterPanel.sortUsersBy'>
                        {(msg) =>
                            <TextField
                                labelId="selectlabel"
                                value={tableSort}
                                label={msg}
                                select
                                variant="filled"
                                onChange={(e) => {
                                    handleSort(e);
                                    setSort(e.target.value);
                                    console.dir(e.target.value);
                                }}
                                >
                                <MenuItem key={1} value={'Date of register'}> <FormattedMessage  id='filterPanel.dateOfRegister' /></MenuItem>
                                <MenuItem key={2} value={'Date of last visit'}><FormattedMessage  id='filterPanel.dateOfLastVisit' /></MenuItem>
                            </TextField>}
                    </FormattedMessage>

                </FormControl>
                <div className={getOrderClasses()}  onClick={()=>{orderHandler()}}>
                    {IconPack.searchComponents.orderDown}
                </div>


                <Button variant="contained" className="btn btn--blue filterPanel__add"  onClick={()=>{
                    props.setCurrentTab('Generate');
                    props.setAdditionalScreenCorporate(true);
                }}>
                    {IconPack.searchComponents.add}
                </Button>

            </div>

            <div>
                {searchValue.length ? searchedData : tbodyData }
            </div>
        </div>
    );
}


const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setCosts: (bool, id) => dispatch(setCosts(bool, id)),
        setDropdown: (bool, id) => dispatch(setDropdown(bool, id)),
        setUsers: (bool, id) => dispatch(setUsers(bool, id)),
        setActivity: (bool, id) => dispatch(setActivity(bool, id)),
        setSelect: (bool, id) => dispatch(setSelect(bool, id)),
        setCurrentTab: (prop) => dispatch(setCurrentTab(prop)),
        setAdditionalScreenCorporate: (bool) => dispatch(setAdditionalScreenCorporate(bool)),
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
        sortTableByType: (type) => dispatch(sortTableByType(type)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CorporateUsers);